<template>
	<div class="app-wrap">
		<div class="view-header" :style="{ display: bottomHide && !showHeader ? 'flex' : 'none' }">
			<div class="back-block" v-on:click="$router.back">
				<span class="iconfont icon-back"></span>
			</div>
		</div>
		<div class="detail-header" :style="{ display: showHeader ? 'flex' : 'none' }">
			<div class="back-block" v-on:click="$router.back">
				<span class="iconfont icon-back"></span>
			</div>
			详情
		</div>
		<div class="view-wrap">
			<router-view></router-view>
		</div>

		<div class="menu-wrap" :style="{ display: bottomHide ? 'none' : 'flex' }">
			<router-link
				v-for="path in menus"
				:key="path.title"
				:to="path.path"
				:class="{ active: path.active }"
			>
				<div :class="`iconfont ${path.icon}`"></div>
				{{ path.title }}</router-link
			>
		</div>
	</div>
	<!-- 路由出口 -->
	<!-- 路由匹配到的组件将渲染在这里 -->
</template>

<script>
// import HomePage from "./components/HomePage.vue";
import routes from "./routes";
import alertOnClick from "./utils/aframeComponents/alertOnClick";
import changeColorOnHover from "./utils/aframeComponents/changeColorOnHover";
import visibleByDistance from "./utils/aframeComponents/visibleByDistance";
import hoverOnBuilding from "./utils/aframeComponents/hoverOnBuilding";
import lableRenderer from "./utils/aframeComponents/lableRenderer";
export default {
	data() {
		return {
			menu: routes,
		};
	},
	name: "App",
	components: {},
	mounted() {
		// document.body.style.height = window.innerHeight;
		// document.body.style.width = window.innerWidth;
		alertOnClick();
		changeColorOnHover();
		visibleByDistance();
		hoverOnBuilding();
		lableRenderer();
	},
	computed: {
		menus: function () {
			return this.menu
				.filter((v) => !!v.title)
				.map((v) => {
					v.active = this.$route.fullPath == v.path;
					return v;
				});
		},
		bottomHide: function () {
			return (
				this.$route.fullPath.includes("/second") || this.$route.fullPath.includes("/third")
			);
		},
		showHeader: function () {
			return this.$route.fullPath.includes("/third");
		},
	},
};
</script>

<style lang="scss">
#app {
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	text-align: center;
	color: #2c3e50;
	/* margin-top: 60px; */
}
.app-wrap {
	height: 100%;
	display: flex;
	flex-direction: column;
	padding-bottom: 70px;
	.view-wrap {
		flex-grow: 1;
	}
	.view-header {
		z-index: 1;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		height: 50px;
		.back-block {
			height: 100%;
			width: 50px;
			text-align: center;
			line-height: 50px;
		}
		.iconfont {
			font-size: 24px;
			color: #fff;
		}
	}
	.detail-header {
		height: 50px;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		background: linear-gradient(91deg, #606c88 0%, #3f4c6b 100%);
		color: #fff;
		font-size: 14px;
		.back-block {
			position: absolute;
			left: 0px;
			height: 100%;
			width: 50px;
			text-align: center;
			line-height: 50px;
		}
		.iconfont {
			font-size: 20px;
			color: #fff;
		}
	}
	.menu-wrap {
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		box-sizing: border-box;
		background: #fff;
		display: flex;
		flex-grow: 0;
		align-items: center;
		justify-content: space-between;
		height: 70px;
		padding: 0 50px;
		.iconfont {
			font-size: 25px;
		}
		a {
			text-decoration: none;
			color: rgba(230, 225, 227, 1);
			&.active {
				color: rgba(96, 108, 136, 1);
			}
		}
	}
}
