// import * as THREE from "three";
// import { OrbitControls } from "../obControls";
import { CSS2DRenderer, CSS2DObject } from "../css2Object";
import store from "../../stores/GlogalStore";

function registry() {
	window.AFRAME.registerComponent("label-renderer", {
		schema: {
			id: { default: "0" },
			type: { default: "build" },
		},

		init: function () {
			const el = this.el; // <a-box>
			const data = this.data;
			const _camera = document.querySelector("a-camera").object3D;
			const camera = _camera.children[1];
			const scene = el.sceneEl.object3D;
			const earthDiv = document.createElement("div");
			earthDiv.className = "label";
			earthDiv.innerHTML = `<img src='${data.type}.png' class="renderer-icon" />`;
			earthDiv.style.marginTop = "-1em";

			const earthLabel = new CSS2DObject(earthDiv);
			earthDiv.addEventListener("click", () => {
				if (LabelRendererService.activeLabelDom) {
					LabelRendererService.activeLabelDom.innerHTML = `<img src='${data.type}.png' class="renderer-icon" />`;
				}
				earthDiv.innerHTML = `<img src='${data.type}-active.png' class="renderer-icon" />`;
				LabelRendererService.activeLabelDom = earthDiv;
				store.commit({
					type: "setBuildingId",
					building_id: data.id,
				});
			});
			earthLabel.position.set(0, 0, 0);
			el.object3D.add(earthLabel);
			LabelRendererService.init(camera, scene);
		},
		tick: function () {},
	});
}

export default registry;
let animateRef = null;
export const LabelRendererService = {
	rendererDom: null,
	animateRef: null,
	activeLabelDom: null,
	init(camera, scene) {
		if (!this.rendererDom) {
			const labelRenderer = new CSS2DRenderer();
			labelRenderer.setSize(window.innerWidth, window.innerHeight);
			labelRenderer.domElement.style.position = "absolute";
			labelRenderer.domElement.style.top = "0px";
			labelRenderer.domElement.style.pointerEvents = "none";
			labelRenderer.domElement.id = "labelRenderer";
			document.body.appendChild(labelRenderer.domElement);
			this.rendererDom = labelRenderer.domElement;

			const animate = () => {
				animateRef = requestAnimationFrame(animate);
				labelRenderer.render(scene, camera);
			};
			animate();
		}
	},
	clear() {
		if (this.rendererDom) {
			document.body.removeChild(this.rendererDom);
			this.rendererDom = null;
			cancelAnimationFrame(animateRef);
		}
	},
};
