<template>
	<a-scene vr-mode-ui="enabled: false" embedded>
		<a-asset>
			<img id="build-image" src="/build.png" />
			<img id="build-active-image" src="/build-active.png" />
			<img id="mall-image" src="/mall.png" />
			<img id="mall-active-image" src="/mall-active.png" />
		</a-asset>
		<a-camera
			id="camera"
			rotation-reader
			gps-camera="simulateLatitude: 29.864398;simulateLongitude: 121.614026;simulateAltitude: 0"
		>
			<a-entity
				cursor="fuse: true; fuseTimeout: 500"
				position="0 0 -1"
				geometry="primitive: ring; radiusInner: 0.001; radiusOuter: 0.001"
				material="color: transparent; shader: flat"
			>
			</a-entity>
		</a-camera>
		<a-ring
			:visible-by-distance="`distance:0,400;id:${item.id}`"
			v-for="item in builds"
			:src="item.image"
			:key="item.id"
			:gps-entity-place="item.place"
			radius-inner="0.001"
			radius-outer="0.002"
			:position="`0 ${item.height} 0`"
			:label-renderer="`id:${item.id}; type:${item.type}`"
		>
		</a-ring>
	</a-scene>
	<img src="/robot.png" class="robot" />
	<div class="info-window" v-html="infoContent"></div>
</template>

<script>
import axios from "axios";
import PAGE_PATH from "../constants/page_path";
import { LabelRendererService } from "../utils/aframeComponents/lableRenderer";
export default {
	name: "HomePage",
	data() {
		return {
			builds: [],
			floors: [],
		};
	},
	unmounted() {
		const video = document.querySelector("#arjs-video");
		if (video) {
			document.body.removeChild(video);
		}
		this.$store.commit({
			type: "setBuildingId",
			building_id: undefined,
		});
		LabelRendererService.clear();
	},
	updated() {
		const btn = document.querySelector(".detail-btn");
		if (btn) {
			btn.addEventListener("click", () => {
				console.log(
					this.$router.push(
						PAGE_PATH.DETAIL_PAGE.replace(":id", this.$store.state?.building_id),
					),
				);
			});
		}
	},
	mounted() {
		axios.get("/build.json").then((res) => {
			res.data.data
				.filter((v) => !!v.geometry)
				.forEach((featrue) => {
					const position = featrue.geometry.coordinates.flat(10);
					const malls = ["宏泰广场", "宁波文化广场", "宁波阪急百货"];
					const [longitude, latitude] = position;
					const item = {
						...featrue.properties,
						height: featrue.properties.height * 0.5,
						place: "longitude:" + longitude + "; latitude:" + latitude,
						image: malls.includes(featrue.properties.building_name)
							? "#mall-image"
							: "#build-image",
						type: malls.includes(featrue.properties.building_name) ? "mall" : "build",
					};
					this.builds.push(item);
				});
		});
	},
	computed: {
		infoContent() {
			const build = this.builds.find((v) => v?.id == this.$store.state?.building_id);
			if (build) {
				const { building_name, address, building_introduction } = build;
				return `<div class='info'>
									<div class="left">
										<img src="/detail-icon.png" />
									</div>
									<div class="right">
										<div class="right-header">
											<div class="name">${building_name}</div>
											<div class="address">${address}</div>
											<div class="detail-btn">
												<span class="iconfont icon-position"></span>
												查看详情
											</div>
										</div>
										<div class="right-content">
											${building_introduction}
										</div>
									</div>
								</div>`;
			} else {
				return "<div class='info'>请点击想要识别的楼宇图标<div>";
			}
		},
	},
};
</script>

<style lang="scss">
.robot {
	position: fixed;
	left: 0;
	bottom: 50px;
	width: 20vw;
}
// canvas {
// 	aspect-ratio: 1 !important;
// 	height: auto !important;
// 	width: 100% !important;
// }
.info-window {
	position: fixed;
	left: 21vw;
	bottom: 50px;
	width: 70vw;
	height: 100px;
	opacity: 1;
	display: flex;
	align-items: center;

	.info {
		width: 100%;
		display: flex;
		justify-content: center;
		background: #ffffff;
		border-radius: 10px;
		min-height: 2em;
		max-height: 100%;
		color: #4f5861;
		font-size: 12px;
		padding: 10px;
		.left {
			width: 60px;
			text-align: center;
			display: flex;
			align-items: flex-start;
			justify-content: center;
			height: 120%;
			margin-right: 10px;
			img {
				width: 30px;
			}
		}
		.right {
			display: flex;
			flex-direction: column;
			overflow: hidden;

			.right-header {
				position: relative;
				text-align: left;
				padding-bottom: 5px;
				border-bottom: 1px solid rgba(71, 126, 251, 0.15);
				.name {
					font-size: 14px;
					color: rgba(17, 31, 44, 1);
					line-height: 1;
					margin-bottom: 4px;
				}
				.address {
					font-size: 12px;
					color: rgba(81, 94, 123, 0.35);
					line-height: 1;
				}
				.detail-btn {
					position: absolute;
					right: 0;
					top: 0px;
					background: #e7eefd;
					font-size: 12px;
					line-height: 20px;
					border-radius: 6px;
					color: rgba(71, 126, 251, 1);
					padding: 0 10px;
					.iconfont {
						font-size: 12px;
					}
				}
			}
			.right-content {
				padding-top: 5px;
				font-size: 12px;
				color: rgba(81, 94, 123, 0.6);
				text-indent: 2em;
				word-break: break-all;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 3;
				-webkit-box-orient: vertical;
			}
		}
	}
}
#labelRenderer {
	pointer-events: none;
	div {
		pointer-events: auto;
	}
}
.renderer-icon {
	width: 100px;
}
</style>
