import store from "../../stores/GlogalStore";

function registry() {
	window.AFRAME.registerComponent("event-on-hover", {
		schema: {
			id: { default: "" },
		},

		init: function () {
			var data = this.data;
			var el = this.el; // <a-box>
			const defaultIcon = el.getAttribute("src");
			const hoverIcon = data.hoverIcon;
			el.addEventListener("mouseenter", function () {
				if (el.object3D.visible) {
					el.setAttribute("src", hoverIcon);
					el.setAttribute("label-renderer", "1");
					store.commit({
						type: "setBuildingId",
						building_id: data.id,
					});
				}
			});
			el.addEventListener("mouseleave", function () {
				el.setAttribute("src", defaultIcon);
			});
		},
	});
}

export default registry;
