// import * as THREE from "three";
function registry() {
	window.AFRAME.registerComponent("visible-by-distance", {
		schema: {
			distance: { default: "0,300" },
			id: { default: "" },
		},
		init: function () {
			var scene = this.el.sceneEl;
			const camera = scene.querySelector("a-camera");
			this.camera = camera.object3D;
			this.cameraChild = this.camera.children?.[1];
		},

		tick: function () {
			const to = this.el.object3D.position;
			// const cloneTo = to.clone();
			// let tempV = cloneTo
			// 	.applyMatrix4(this.cameraChild.matrixWorldInverse)
			// 	.applyMatrix4(this.cameraChild.projectionMatrix);

			// if (Math.abs(tempV.x) > 1 || Math.abs(tempV.y) > 1 || Math.abs(tempV.z) > 1) {
			// 	// 在视野外了
			// 	// console.log(tempV, this.data.id);
			// 	this.el.object3D.visible = false;
			// 	return;
			// } else {
			// 	// 在视野内
			// 	this.el.object3D.visible = true;
			// }
			const from = this.camera.position;
			const distance = from.distanceTo(to);
			const [near, far] = this.data.distance.split(",");
			if (distance > +near && distance < +far) {
				this.el.object3D.visible = true;
				this.el.object3D.rotation.y = this.camera.rotation.y;
				this.el.object3D.children.forEach((v) => {
					if (v.isCSS2DObject) {
						v.visible = true;
					}
				});

				// console.log(this.data.id);
			} else {
				this.el.object3D.visible = false;
				this.el.object3D.children.forEach((v) => {
					if (v.isCSS2DObject) {
						v.visible = false;
					}
				});
			}
		},
	});
}

export default registry;
