<template>
	<div class="detail-page-wrap">
		<div class="detail-content">
			<div class="detail-item" v-for="item in detail" :key="item.label">
				<div class="item-label">{{ item.label }}</div>
				<div class="item-value" :class="item.class">{{ item.value }}</div>
			</div>
		</div>
	</div>
</template>
<script>
import axios from "axios";

export default {
	name: "DetailPage",
	data() {
		return {
			builds: [],
		};
	},
	mounted() {
		axios.get("/build.json").then((res) => {
			res.data.data
				.filter((v) => !!v.geometry)
				.forEach((featrue) => {
					this.builds.push(featrue.properties);
				});
		});
	},
	computed: {
		detail() {
			const detailItems = {
				building_name: "楼宇名称",
				address: "座落地址",
				building_type: "楼宇类型",
				builtup_area: {
					label: "建筑面积",
					render: (v) => `${v || ""} m²`,
					class: "number",
				},
				reference_rent: {
					label: "参考租金",
					render: (v) => `${v || ""} m²*天`,
					class: "number",
				},
				reference_property: {
					label: "参考物业费",
					render: (v) => `${v || ""} m²*月`,
					class: "number",
				},
				business_area: {
					label: "商务面积",
					render: (v) => `${v || ""} m²`,
					class: "number",
				},
				building_developer: "楼宇开发商",
				property_company: "物业公司",
				property_manager: "物业负责人",
				building_introduction: "楼宇简介",
			};
			console.log(detailItems);
			const item = this.builds.find((v) => v.id == this.$route.params.id) || {};
			console.log(item);
			return Object.keys(detailItems).map((key) => {
				const keyValue = detailItems[key];
				const label = typeof keyValue == "object" ? keyValue.label : keyValue;
				const value = typeof keyValue == "object" ? keyValue.render(item[key]) : item[key];
				return {
					label,
					value,
					class: keyValue.class,
				};
			});
		},
	},
};
</script>
