.detail-page-wrap {
	background: rgba(244, 244, 244, 1);
	height: calc(100vh - 50px);
	box-sizing: border-box;
	overflow: hidden;
	padding: 15px;
	.detail-content {
		background: #fff;
		height: 100%;
		border-radius: 5px;
		overflow-y: scroll;
		overflow-x: hidden;
		padding: 10px 15px;
		max-width: 100%;
		box-sizing: border-box;
		.detail-item {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			min-height: 22px;
			font-size: 14px;
			line-height: 1.5;
			border-bottom: 1px solid rgba(112, 112, 112, 0.2);
			margin-top: 10px;

			.item-label {
				color: rgba(153, 153, 153, 1);
				white-space: nowrap;
				margin-right: 20px;
			}
			.item-value {
				&.number {
					color: rgba(71, 126, 251, 1);
				}
				text-align: left;
			}
		}
	}
}
</style>
