<template>
	<div class="home-wrap">
		<div class="iframe-wrap" id="map-container"></div>
	</div>
</template>
<script>
import AmapService from "../utils/AmapService";
import BORDER from "../constants/border";
import PAGE_PATH from "@/constants/page_path";
export default {
	name: "ARHome",
	data() {
		return {
			src: "",
		};
	},

	mounted() {
		// navigator.geolocation.getCurrentPosition((position) => {
		// 	const {
		// 		coords: { latitude, longitude },
		// 	} = position;
		// 	this.src += `https://m.amap.com/navi?&dest=${longitude},${latitude}&destName=我的位置&hideRouteIcon=1&key=02cc8687642e39d39a9475b659aaffba`;
		// });
		const _this = this;
		AmapService.getAmap().then((AMap) => {
			const map = new AMap.Map("map-container", {
				zoom: 12,
			});
			AMap.plugin("AMap.Geolocation", function () {
				var geolocation = new AMap.Geolocation({
					// 是否使用高精度定位，默认：true
					enableHighAccuracy: true,
					// 设置定位超时时间，默认：无穷大
					timeout: 5000,
					// 定位按钮的停靠位置的偏移量
					offset: [10, 20],
					//  定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
					zoomToAccuracy: true,
					//  定位按钮的排放位置,  RB表示右下
					position: "RB",
				});

				map.addControl(geolocation);
				geolocation.getCurrentPosition(function (status, result) {
					if (status == "complete") {
						onComplete(result);
					} else {
						onComplete(result);
					}
				});

				function onComplete(data) {
					// data是具体的定位信息

					var inRing = AMap.GeometryUtil.isPointInRing(data.position, BORDER);

					if (inRing) {
						// alert("目前只支持在福明街道范围内使用");
						let infoWindow = new AMap.InfoWindow({
							anchor: "bottom-center",
							content:
								"<div class='warn'><img src='/warn.png' />目前只支持在福明街道范围内使用</div>",
						});
						infoWindow.open(map, data.position);
						infoWindow.on("open", (e) => {
							console.log(e);
						});
					} else {
						let infoWindow = new AMap.InfoWindow({
							anchor: "bottom-center",
							content: "<div class='entrance'>AR识别(请面朝正北打开AR识别)</div>",
						});
						infoWindow.open(map, data.position);
						if (infoWindow.getIsOpen()) {
							document.querySelector(".entrance")?.addEventListener("click", () => {
								console.log(_this.$router.push(PAGE_PATH.AR_MAIN));
							});
						}
					}
				}

				// function onError() {
				// 	// 定位出错
				// 	let infoWindow = new AMap.InfoWindow({
				// 		anchor: "bottom-center",
				// 		content:
				// 			"<div class='warn'><img src='/warn.png' />目前只支持在福明街道范围内使用</div>",
				// 	});
				// 	infoWindow.open(map, map.getCenter());
				// }
			});

			var path = BORDER.map((v) => new AMap.LngLat(...v));

			var polygon = new AMap.Polygon({
				path: path,
				fillColor: "rgba(61, 158, 250, .2)", // 多边形填充颜色
				borderWeight: 0, // 线条宽度，默认为 1
				strokeColor: "transparent", // 线条颜色
			});

			map.add(polygon);
		});
	},
};
</script>
<style lang="scss">
.home-wrap {
	height: 100%;
	.iframe-wrap {
		position: relative;
		height: 100%;
	}
	.amap-info-outer {
		border: 2px solid #ffffff;
		box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
		opacity: 1;
		padding: 0;
		border-radius: 15px;
	}
	.amap-info-close {
		display: none;
	}
	.warn {
		padding: 10px 20px;

		img {
			width: 25px;
			margin: 0 10px -4px 0;
		}
	}
	.entrance {
		background: linear-gradient(180deg, #3d9efa 0%, #477efb 100%);
		padding: 10px 20px;
		color: #fff;
	}
}
</style>
